:root {
    --bgColour: rgb(10, 1, 37);
}

#app {
    background-color: var(--bgColour);
    min-height: 100vh;
    color: white;
    padding-bottom: 3vh;
}

.header {
    display: flex;
    justify-content: center;
    text-align: center;
    background: linear-gradient(180deg, rgba(6, 0, 24, 0.925) 0%, rgba(10, 1, 37, 0.062) 72%);
    padding-top: 1rem;
}

.header a {
    text-decoration: none;
    color: white;
    user-select: none;
    font-size: 5vh;
    font-weight: 600;
}

.header a:hover {
    transition: opacity 10.5s ease;
}


.container {
    text-align: center;
    min-height: 50vh;
    margin: 5vh 10vw;
    padding: 3vh;
    background-color: rgba(163, 208, 248, 0.24);
    border-radius: 2rem;
}

#filebox {
    padding: 3vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#file-upload {
    width: 0;
    position: absolute;
    overflow: hidden;
    z-index: -1;
}

#file-upload+#file-label {
    cursor: pointer;
    background-color: aliceblue;
    color: black;
    font-size: 5vh;
    padding: 1.5vh;
    border: #000;
    border-radius: 0.75rem;
    outline: 2px solid #2e2c2c;
}

#file-upload:hover+#file-label:hover {
    outline: 3.5px solid #000000;
    outline: -webkit-focus-ring-color auto 5px;
    background-color: rgb(53, 103, 179);
    font-weight: 600;
}

#file-upload:active+#file-label:active {
    outline: 5.5px solid #000000;
    outline: -webkit-focus-ring-color auto 5px;
    background-color: rgb(53, 103, 179);
    font-weight: 600;
}


#radiobox {
    padding: 3vw;
    font-size: 4vh;
    display: grid;
    grid-template-columns: auto auto;
    gap: 0.5em;
}

#radiobox input {
    height: 2.5rem;
}

#msgbox {
    margin: 1vh;
    padding: 2vh;
}

#msgbox input {
    width: 95%;
    font-size: 1.5rem;
    border-radius: 1rem;
}

#buttonbox {
    padding: 1vh;
}

#buttonbox button {
    cursor: pointer;
    color: black;
    font-size: 5vh;
    padding: 1.5vh;
    border: #000;
    border-radius: 0.75rem;
    outline: 2px solid #2e2c2c;
}


.processed-title {
    font-size: 3rem;
    padding-bottom: 1rem;
    text-decoration: underline;
}

.encoded-box {
    height: 100%;
}

.encoded-image {
    max-width: 100%;
    max-height: 100%;
}

.secret-message {
    background-color: aliceblue;
    color: #000;
    border-radius: 2rem;
    font-size: 2.5rem;
}
